<script>
import { mapActions, mapGetters } from "vuex";
import { setTimeout } from "timers";

export default {
  name: "UnderwritingDetails",
  data() {
    return {
      subOptions: [],
      bussinessType: "",
      routeDate: "",
      activeNames: [],
      underwritingDetails: {
        taskProcessing: {}, //
        summaryInfo: {}, //
        underwriteOpinion: [], // 
        uwpTexts: {}, // 
        uwinsuredT: {}, // 
        uwinsuredI: {}, // 
        uwinsuredC: {}, // 
        uwitemCar: {}, // 
        uwcarmodel: {}, // 
        insurancePeriod: {}, // 
        uwitemkindZ: [], // 
        uwitemkindF: [], // 
        payTravelTaxInfo: {}, //
        uwqfixationDetails: [], // 
        uwcardrivers: [], // 
        uwengages: [], // 
        generalInformation: {}, // 
        otherInformation: {}, // 
        uwitemkindCount: {}, // 
        uwNotion: {}, // 
        displayFlag: {
          browseFlag: "0", // 
          flowRecordFlag: "0", //	
          ClaimFlag: "0", //	
          lastPolicyClaimFlag: "0", //	
          coverFacEnquiryFlag: "0", //	
          headOfficeInfoFlag: "0", //	
          branchOfficeInfoFlag: "0", //	
          relationImageFlag: "0", //	
          infoFlag: "0", //	
          uploadImageFlag: "0", //	
          mobileImageFlag: "0", //	
          deviceInfoFlag: "0", //	
          shipInfoFlag: "0", //	
          lastPolicyInfoFlag: "0", //	
          revokeFlag: "0", //	
          riskTypeRateFlag: "0", //	
          reportFormsFlag: "0", //	
          possessTaskFlag: "0", // 
          approvalInfoFlag: "0" //	
        },
        uwprofitdetails: [] //  -- 
      },
      //  ---
      uwNotionFlags: [
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" }
      ],
      //  ---
      uwNotionCarCheckStatus: [
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" },
        { value: "4", label: "" },
        { value: "5", label: "" }
      ],
      tableList: [{}],
      value: "",
      relationDialog: false,
      aprove: {},
      rules: {},
      radio: 1,
      tableData: [], //   
      outerVisible: false,
      innerVisible: false,
      adjustRateDialog: false, //  
      textarea1: "",
      condition: "0",
      textarea2: "",
      options: [
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        }
      ]
    };
  },

  methods: {
    //collapse
    setActiveNames() {
      for (let i = 1; i <= 23; i++) {
        this.activeNames.push(JSON.stringify(i));
      }
    },
    // 
    goback() {
      this.$router.go(-1);
    },
    // 
    getBack() {
      let keyWords = {
        businessNo: this.routeDate.businessNo || "AST12312312",
        ComCode: this.routeDate.businessNo || "BJ233",
        UserCode: this.routeDate.businessNo || "WPC212",
        UserName: this.routeDate.businessNo || "",
        revokeType: this.routeDate.type || "EH", //  1  2  3
        taskId: this.routeDate.businessNo || "id12312", // id
        businessType: this.routeDate.type || "ST",
        batchNo: this.routeDate.type || 12312312
      };
      this.$fetch
        .post(this.HOST + this.$url.undwrtrevokeUndwrt, keyWords)
        .then(data => {
          console.log(data);
          this.$message.success(data);
        });
    },
    // 
    giveUp() {
      let key = {
        businessNo: this.parameter.businessNo,
        businessType: this.parameter.businessType,
        usercode: "A000"
      };
      this.$fetch.post(this.HOST + this.$url.giveUpUwPayee, key).then(data => {
        console.log(data);
        this.$message.success(data);
        setTimeout(() => {
          this.goback();
        }, 1500);
      });
    },
    // 
    submit() {
      let key = {
        businessType: "T",
        businessNo: "TDZA201945010000014005",
        nodeName: "",
        editType: "submit",
        curNode: "301"
      };
      this.$fetch
        .post(this.HOST + this.$url.undwrtSubmitReview, key)
        .then(data => {
          console.log(data);
          this.subOptions = data.mapList;
          console.log(this.subOptions);
          this.aprove.handleText = data.uwnotion.handleText;
          this.outerVisible = true;
        });
    },
    // 
    submitReview() {
      let keyWords = {
        businessType: "T",
        businessNo: "TDZA201945010000014005",
        nodeCode: this.aprove.nodeCode,
        prepusercode: "", // code
        uwnotion: {
          handleText:
            this.aprove.handleText +
            this.aprove.inspSpeak +
            ";" +
            this.aprove.remark
        }
      };
      let url = "";
      switch (this.aprove.nodeCode) {
        case "000": // 
          url = this.HOST + this.$url.undwrtendTask;
          break;
        case "999": // 
          url = this.HOST + this.$url.undwrtworkReject;
          break;
        default:
          // 
          url = this.HOST + this.$url.undwrtSubmitToExamine;
          break;
      }
      this.$fetch.post(url, keyWords).then(data => {
        console.log(data);
        this.outerVisible = false;
        this.innerVisible = true;
      });
    },
    //
    init() {
      let keyWords = {
        request: "INFO", // 
        businessNo: this.routeDate.businessNo, // 
        taskType: this.routeDate.type, // 
        taskId: this.routeDate.businessNo, //  Id
        businessType: this.routeDate.type, // businessType
